<template>
<div class="from-container">
  <h1 class="ptitle"></h1>
   <p class="control has-icon">
  <input class="input" type="text" v-model="username" placeholder="">
  <i class="fa fa-envelope"></i>
</p>
<p class="control has-icon">
  <input class="input" type="password" v-model="password" placeholder="">
  <i class="fa fa-lock"></i>
</p>
<p class="control">
  <button class="button is-success is-fullwidth" @click="login()" >
    
  </button>
</p>
  <div class="center">
    <router-link to="login" class=" is-outlined " ></router-link>
  </div>
</div>

</template>


<script>
import * as _ from "lodash";

export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    login() {
      if (!this.username) {
        this.$modal.alert({
          content: ""
        });
      } else if (!this.password) {
        this.$modal.alert({
          content: ""
        });
      } else {
        this.$http
          .post("/api/user/register", {
            username: this.username,
            password: this.password
          })
          .then(data => {
            if (data) {
              localStorage.setItem("user", JSON.stringify(data));
              this.$router.push("home");
            } else {
              this.$modal.alert({
                content: ""
              });
            }
          });
      }
    }
  }
};
</script>



